export default {
  props: ['data'],
  data: function data() {
    var _this = this;

    var checkAmount = function checkAmount(rule, value, callback) {
      if (value < 50) {
        callback(new Error('The minimum withdrawal amount is 50'));
      } else if (value > _this.data.withdrawal) {
        callback(new Error('Can not exceed the amount available for withdrawal'));
      } else {
        callback();
      }
    };

    return {
      withdrawalHistoryLoading: false,
      withdrawalHistory: false,
      historyLoading: false,
      pageSizes: [10, 20, 30],
      page: 1,
      rowsPerPage: 10,
      pageW: 1,
      rowsPerPageW: 10,
      total: 0,
      totalPage: 0,
      historyVisible: false,
      innerVisible: false,
      host: window.location.host,
      historyData: [],
