        amount: [{
          required: true,
          message: 'Please enter the withdrawal amount',
          trigger: 'blur'
        }, {
          validator: checkAmount,
          trigger: 'blur'
        }],
        account: [{
          required: true,
          message: 'Please enter your account',
          trigger: 'blur'
        }],
        name: [{
          required: true,
          message: 'Please enter your account name',
          trigger: 'blur'
        }]
      }
    };
  },
