      var _this4 = this;

      this.$apiCall('api.User.getInviteInfo', {}, function (r) {
        if (r.ErrorCode == "9999") {
          _this4.data.inviteCode = r.Data.Results.inviteCode;
          _this4.data.orderRewards = r.Data.Results.orderRewards;
          _this4.data.items = r.Data.Results.invitedUsers;
          _this4.data.withdrawalAmount = r.Data.Results.withdrawalAmount;
          _this4.data.withdrawal = r.Data.Results.withdrawal;
          _this4.data.canWithdrawal = r.Data.Results.canWithdrawal;
        }
      });
    },
    withdrawal: function withdrawal() {
      var _this5 = this;

      this.$refs['messageForm'].validate(function (valid) {
        if (valid) {
          var params = _this5.form;

          _this5.$apiCall("api.User.withdrawal", params, function (r) {
            if (r.ErrorCode == "9999") {
              _this5.$elementMessage('Submitted successfully, please wait for approval', "success");

              _this5.innerVisible = false;
              _this5.form = {
                amount: undefined,
                account: '',
